
pre {
  white-space: pre;
  word-wrap: normal;
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #f5f5f5 !important;
  color: #000 !important;
  white-space: pre;
  word-break: normal;
  border: 0 !important;
}
pre code {
  border: 0;
  box-shadow: none;
}
.hljs,
.hljs-tag,
.hljs-subst {
  color: #000;
}
.hljs-strong,
.hljs-emphasis {
  color: #a8a8a2;
}
.hljs-bullet,
.hljs-quote,
.hljs-number,
.hljs-regexp,
.hljs-literal,
.hljs-link {
  color: #ae81ff;
}
.hljs-code,
.hljs-title,
.hljs-section,
.hljs-selector-class {
  color: #a6e22e;
}
.hljs-strong {
  font-weight: bold;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-keyword,
.hljs-selector-tag,
.hljs-name,
.hljs-attr {
  color: #f92672;
}
.hljs-symbol,
.hljs-attribute {
  color: #66d9ef;
}
.hljs-params,
.hljs-class .hljs-title {
  color: #f8f8f2;
}
.hljs-string,
.hljs-type,
.hljs-built_in,
.hljs-builtin-name,
.hljs-selector-id,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-addition,
.hljs-variable,
.hljs-template-variable {
  color: #42A7E2;
}
.hljs-comment,
.hljs-deletion,
.hljs-meta {
  color: #75715e;
}



